<template>
<div class="artists">
 <p class="nav"><img @click="back" src="../assets/img/back2.png"><span> 歌手馆 </span></p>
 <div class="hot-art">
     <h2>热门</h2>
     <ul class="hot">
         <li v-for="hot in hot_art" :key="hot.id">
             <router-link tag="div" :to="`/recommend_songs/${hot.id}`">
             <img :src="`${hot.picUrl}`">
             </router-link>
             <i>{{hot.name}}</i>
         </li>
     </ul>
<div class="list">
    <ul>
        <li @click="cat_art=1001" :class="{active:cat_art == 1001}">华语男歌手</li>
        <li @click="cat_art=1002" :class="{active:cat_art == 1002}">华语女歌手</li>
        <li @click="cat_art=1003" :class="{active:cat_art == 1003}">华语乐队</li>
        <li @click="cat_art=2001" :class="{active:cat_art == 2001}">欧美男歌手</li>
        <li @click="cat_art=2002" :class="{active:cat_art == 2002}">欧美女歌手</li>
        <li @click="cat_art=7003" :class="{active:cat_art == 7003}">韩国乐队</li>
    </ul>
</div>
<div class="content">
    <p v-for="one in list_art" :key="one.id">
    <router-link tag="div" :to="`/recommend_songs/${one.id}`">
        <img :src="`${one.picUrl}`" alt="">
        <i>{{one.name}}<span v-if="one.alias.length>0"> - {{one.alias[0]}}</span></i>
             </router-link>

    </p>
</div>
 </div>
    
    </div>
</template>
<script>
export default {
    data(){
        return{
         hot_art:[],
         cat_art:1001,
         list_art:[]
        }
    },
      methods:{
      back(){
          this.$router.back();
      }
  },
  created(){
      this.$axios.get('/top/artists?offset=0&limit=8').then(data=>{
          this.hot_art = data.data.artists;
      });
      
   this.$axios.get('/artist/list?cat=' + this.cat_art).then(dat=>{
          this.list_art = dat.data.artists;
      });
  },
  watch:{
      cat_art(){
           this.$axios.get('/artist/list?cat='+ this.cat_art ).then(date=>{
          this.list_art = date.data.artists;   
      });
      }
  }
}
</script>

<style lang="less" scoped>
  h2{
          font-size: 18px;
          margin: 10px;
          color: black;
          letter-spacing: 3px;
      }

 .artists{
     
      .nav{
        display: flex;
        height: 40px;
        width: 100vw;
        font-size: 20px;
        align-items: center;
        img{
            width: 25px;
            height: 20px;
            margin-left: 10px;
        }
        span{
            margin: 0 auto;
        }
    }
    .hot-art{
        ul{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            border-bottom: 10px solid rgb(248, 247, 247);
            li{
                width: 23%;
                text-align: center;
                font-size: 14px;
                margin: 10px 0;
                img{
                    width: 20vw;
                    height: 18vw;
                    border-radius: 50%;
                }

            }
        }
    }
    .list{
     ul{
         li{
          padding: 5px;
          background-color: rgb(245, 244, 244);
          border-radius: 20px;
         }
         li.active{
          background-color: rgb(204, 224, 148);
          color: rgb(250, 249, 245);

         }
     }
 }
 .content{
     padding: 10px;
     p{ 
         div{   
        display: flex;
         justify-content: space-between;
         align-items: center;
         margin: 20px 0;
         img{
             width: 18vw;
             height: 17vw;
             border-radius: 5px;
             box-shadow: 0px 2px 3px 0px #666;
         }
         i{
             width: 70vw;
         }
     }
      }
 }

 }
 
</style>